.example-slider {
  @include tablet {
    background-color: $basic-gray;
    margin-left: -30px;
    margin-right: -30px;
    height: 462px;
    margin-top: 216px;
    padding-bottom: 3px;
  }

  @include desktop {
    grid-area: slider;
    position: relative;
    right: -11px;
    bottom: 90px;
    background-color: unset;
  }
}

.example-slider__container {
  text-align: center;

  @include tablet {
    display: flex;
    width: 592px;
    height: 511px;
    position: relative;
    bottom: 165px;
    margin: 0 auto;
  }
}

.example-slider__image img {
  max-width: 100%;
  height: auto;
}

.example-slider__image-wrapper {
  @include tablet {
    overflow: hidden;
    width: 50%;
  }
}

.example-slider__image {
  @include desktop {
    margin-top: -80px;
  }
}

.example-slider__image--fat {
  @include tablet {
    width: 700px;
    height: 100%;
    max-width: none;
    transform: translateX(-26px);
  }

  @include desktop {
    transform: translate(-10px, 75px);
  }
}

.example-slider__image--slim {
  @include mobile-only {
    display: none;
  }

  @include tablet {
    width: 570px;
    height: 100%;
    max-width: none;
    transform: translateX(-256px);
  }

  @include desktop {
    transform: translate(-256px, 80px);
  }
}

.example-slider__control {
  width: calc((100% / 3) * 2);
  margin: 9px auto 0;
  display: flex;
  align-items: center;
  column-gap: 16px;
  justify-content: center;

  @include tablet {
    margin-top: 10px;
    width: 73%;
    min-width: 426px;
    position: relative;
    bottom: 155px;
    column-gap: 22px;
  }
}

.example-slider__control-range {
  width: 100%;
  background-color: #ffffff;
  height: 14px;
  border-radius: 6px;
  position: relative;
}

.example-slider__control-button {
  @include clear-margin;
  @include clear-padding;
  @include mr-s16-h20;

  width: 49px;
  height: 30px;
  text-transform: uppercase;
  border: none;
  color: #000000;
  background-color: transparent;
  border-bottom: 1px dotted rgba(#000000, 0.24);
  cursor: pointer;

  &:hover {
    border-bottom: 1px solid rgba(#000000, 0.4);
  }

  &:active {
    border-bottom: unset;
  }

  @include tablet {
    @include mr-s20-h30;
  }
}
