.main-nav {
  margin-top: 8px;
}

.main-nav__toggle {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: #ffffff;
  padding: 0;
  border: 0;
}
.main-nav__toggle::before {
  content: url("../img/icons/burger-open.svg");
  width: 24px;
  height: 16px;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    display: none;
    position: absolute;
    margin: 65px 0 0;
    left: 0;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    column-gap: 34px;
  }
}

@media (max-width: 767px) {
  .main-nav__item {
    background-color: #ffffff;
    width: 100%;
    height: 66px;
    border-bottom: 1px solid #e6e6e6;
    padding-top: 22px;
    box-sizing: border-box;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .main-nav__item:first-of-type {
    border-top: 1px solid #e6e6e6;
  }
}

.main-nav__link {
  font-size: 18px;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  cursor: pointer;
  position: relative;
}
@media (max-width: 767px) {
  .main-nav__link {
    font-size: 20px;
    font-weight: 500;
    font-style: normal;
    line-height: 20px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link::before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #68b738;
  left: 0;
  top: 33px;
  display: none;
  position: absolute;
}
@media (min-width: 1440px) {
  .main-nav__link::before {
    top: 35px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    font-size: 20px;
    font-weight: 400;
    font-style: normal;
    line-height: 30px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}

@media (min-width: 768px) {
  .main-nav__link--active::before {
    display: block;
  }
}

.main-nav--index {
  z-index: 2;
}

@media (min-width: 1440px) {
  .main-nav--index .main-nav__link::before {
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .main-nav--index .main-nav__link {
    color: #ffffff;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list {
    display: block;
    width: 100%;
    margin-top: 20px;
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle {
    background-color: rgba(1, 1, 1, 0);
  }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 31px;
    right: 20px;
    width: 24px;
    height: 2px;
    background-color: #000000;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
}

